<template>
    <v-expansion-panel>
        <v-expansion-panel-content v-model="openTable">
            <div slot="header" class="pa-2">
                <v-badge right :color="GET_ALERTS ? 'error' : list.length === 0 ? 'accent' :'success'">
                    <v-icon
                        slot="badge"
                        small
                        v-if="GET_ALERTS"
                    >
                        fas fa-exclamation
                    </v-icon>
                    <v-icon v-else-if="list.length === 0"
                      slot="badge"
                      small
                    >
                      fas fa-bell-slash
                      
                    </v-icon>
                    <v-icon v-else
                        slot="badge"

                    >
                        fas fa-check
                    </v-icon>
                    <span
                        style="font-size: 15pt;"
                    >
                        <v-icon>
                            fas fa-bells
                        </v-icon>
                        Alerts
                    </span>
                </v-badge>
            </div>
            <v-card
                :color="GET_ALERTS ? 'error' : 'success'"
                class="pa-2"
            >
                <v-data-table
                    :headers="headers"
                    :items="list"
                    :pagination.sync="pagination"
                >
                    <template slot="no-data">
                      <v-alert color="accent"
                        :value="true"
                        icon="fas fa-bell-slash"
                      >
                        No alerts have been recorded
                      </v-alert>
                    </template>
                    <template slot="items" slot-scope="props">
                        <td>{{ props.item.date }}</td>
                        <td>{{ props.item.time }}</td>
                        <td>{{ props.item.name }}</td>
                        <td>{{ props.item.alert }}</td>
                        <td>{{ props.item.text }}</td>
                        <td>{{ props.item.level }}</td>
                    </template>
                </v-data-table>
            </v-card>
        </v-expansion-panel-content>
    </v-expansion-panel>
</template>

<script>
import { mapState, mapGetters, mapActions } from 'vuex';

export default {
  props: {
    open: Boolean,
  },
  name: 'AlarmList',
  computed: {
    ...mapState({
      list: 'alertsList',
      
    }),
    ...mapGetters([
      'GET_ALERTS',
    ]),

    openTable() {
      return this.open;
    }
  },
  mounted() {
    this.getAlerts();
  },
  data: () => ({
    alarmPresent: false,
    headers: [
      { text: 'Date', align: 'left', value: 'date' },
      { text: 'Time', align: 'left', value: 'time' },
      { text: 'Device Name', value: 'name' },
      { text: 'Alert Subject', value: 'subject', sortable: false },
      { text: 'Alert Message', value: 'message', sortable: false },
      { text: 'Alert Level', value: 'level', sortable: false },
    ],
    pagination: {
      descending: false,
      page: 2,
      rowsPerPage: 5,
    },
  }),
  methods: {
    ...mapActions({
      getAlerts: 'GET_EVENTS',
    }),
  },
};

</script>
