<template>
    <v-container fluid
        style="width: 100%;"
    >
        <v-card width="100%"
            class="pa-2"
            style="border-radius: 10px;"
            height="100%"
        >
            <MapMenu @toggleDevNames="devNames = !devNames"
                @togglePaddocks="padNames = !padNames"
            />
            <GmapMap
                :center="user.farmCenter"
                :zoom="12"
                map-type-id="satellite"
                style="width: 100%; height: 69vh"
            >
                <div id="mapIcons" v-for="marker in gpsMarkers" :key="marker.id">
                    <gmap-custom-marker :marker="marker.gps"
                    >
                        <TrackMarker v-if="marker.trackable === true"
                            :id="marker.id"
                            :species="marker.species"
                            :inAlarm="marker.alarm"
                            :isOutdated="marker.outdated"
                            :assetName="marker.name"
                            :data="marker.data"
                            :time="marker.time"
                        ></TrackMarker>

                        <AssetMarker v-if="marker.trackable === false"
                            :assetName="marker.name"
                            :id="marker.id"
                            :type="marker.species"
                            :inAlarm="marker.alarm"
                            :isOutdated="marker.outdated"
                            :data="marker.data"
                            :time="marker.time"
                        ></AssetMarker>

                    </gmap-custom-marker>

                    <gmap-custom-marker v-if="devNames"
                        :marker="marker.gps"
                        alignment="rightbottom"
                    >
                        <v-card
                            class="pa-1"
                            :class="{ okBox: !marker.alarm && !marker.outdated, alertBox : marker.alarm, outdateBox : marker.outdated }"
                            width="100%" height="15pt"
                        >
                            <p style="font-size: 8pt; font-weight: bold; text-align: center; color: black;">
                                {{ marker.name }}
                            </p>

                        </v-card>
                    </gmap-custom-marker>

                    <gmap-custom-marker v-if="marker.species === 'tank'"
                        :marker="marker.gps"
                        alignment="righttop"
                    >
                        <v-card width="90%" max-height="15pt"
                            class="ml-1 mb-1 py-1"
                            v-if="marker.species === 'tank'"
                            :class="{ okBox: !marker.alarm && !marker.outdated, alertBox : marker.alarm, outdateBox : marker.outdated }"
                        >
                            <p style="font-size: 8pt; font-weight: bold; text-align: center; color: black;">{{ marker.data.percentFull }}%</p>
                        </v-card>
                    </gmap-custom-marker>
                </div>
                <div
                    v-for="(paddock, index) in paddocks"
                    :key="index"
                >
                    <gmap-polygon
                        v-if="padNames"
                        :paths="paddock.gps" :editable="false"
                        :options="polyOptions"
                    ></gmap-polygon>

                    <gmap-custom-marker
                        :marker="paddock.gps[0]"
                        alignment="top"
                        v-if="padNames"

                    >
                        <v-card class="paddockName mb-2"
                            width="100%" height="30%"
                            color="secondary"

                        >
                            <v-card-title class="pa-3"
                                style="font-weight: bold;"
                            >
                                {{ paddock.name }}
                            </v-card-title>
                        </v-card>

                    </gmap-custom-marker>
                </div>
            </GmapMap>
        </v-card>
    </v-container>
</template>

<script>
import TrackMarker from '@/components/map/TrackMarker.vue';
import GmapCustomMarker from 'vue2-gmap-custom-marker';
import AssetMarker from '@/components/map/AssetMarker.vue';
import MapMenu from '@/components/map/MapMenu.vue';
import {
  mapState, mapGetters, mapActions, mapMutations,
} from 'vuex';
import { setInterval } from 'timers';
import firebase from 'firebase';

export default {
  name: 'FarmMap',
  data: () => ({
    devNames: false,
    padNames: false,
    polyOptions: {
      fillColor: '#FFFFFF',
      fillOpacity: 0,
      strokeColor: '#74B3CE',
    },
  }),

  mounted() {
    this.FETCH_DATA();

    setInterval(() => {
      this.FETCH_DATA();
    }, 600000);

 
    //this.MAYB_DATA();
  },

  computed: {
    ...mapGetters([
      'GET_MARKERS',
    ]),

    ...mapState([
      'gpsMarkers',
      'user',
      'paddocks',
    ]),

  },

  components: {
    'gmap-custom-marker': GmapCustomMarker,
    TrackMarker,
    AssetMarker,
    MapMenu,
  },

  methods: {
    ...mapActions([
      'FETCH_DATA',
      'MAYB_DATA',
    ]),
  },
};

</script>

