<template>
    <v-card height="50pt"
        width="155pt" class="black--text pb-5 mb-3" color="secondary" flat
        >
        <v-layout column>
            <p style="font-size: 18px; font-weight: bold;"
                class="mt-1 mb-0 mx-1 font-weight-medium"
            >
                {{ name }}
            </p>

             <p style="font-size: 12px; font-weight: bold;"
                class="mt-0 mb-0 mx-1 font-weight-medium"
            >
                {{ relativeTime(time) }}
            </p>
            <v-layout row>
                <v-tooltip bottom :key="item.tooltip" v-for="item in menu" lazy
                >
                    <v-btn icon slot="activator"
                        :to="item.ref"
                        small
                        class="mx-2 mb-4 mt-1"
                        @click="select({ name: name, id: id, type: type, data: data })"
                    >
                        <v-icon color="black"
                            size="20px"
                        >
                            {{ item.icon }}
                        </v-icon>
                    </v-btn>
                    <span>{{ item.tooltip }}</span>
                </v-tooltip>
            </v-layout>
        </v-layout>
    </v-card>
</template>

<script>
import { mapMutations } from 'vuex';
import TimeAgo from 'javascript-time-ago';
import en from 'javascript-time-ago/locale/en';

export default {
  name: 'OnHover',
  props: {
    name: String,
    id: String,
    time: String,
    type: String,
    data: {},
  },
  data: () => ({
    menu: [
      { tooltip: 'Data History', icon: 'fas fa-chart-line', ref: '/details/data' },
      { tooltip: 'Alert History', icon: 'fas fa-exclamation-triangle', ref: '/details/alerts' },
      { tooltip: 'Diagnostics', icon: 'fas fa-tools', ref: '/details/diag' },
      { tooltip: 'Configurations', icon: 'fas fa-sliders-h', ref: '/details/config' },
    ],
  }),
  methods: {
    ...mapMutations({
      select: 'SELECT_ASSET',
    }),
    relativeTime(timestamp) {
      TimeAgo.addLocale(en);

      const timeAgo = new TimeAgo('en-AU');

      return timeAgo.format(new Date(timestamp));

    }
  },
};
</script>

<style scoped>
    .v-divider {
        border-color: black !important;
    }
</style>
